import React, { useEffect } from "react";
import { Route, Router, Switch } from "react-router-dom";
import history from "./history.js";
import { useDispatch } from "react-redux";

import Navigation from "./shared/navigation/Navigation";
import Footer from "./shared/footer/Footer";
import PrivateRoute from "./Auth/PrivateRoute";

import Home from "./Home/Home";
import Properties from "./Properties/Properties";
import About from "./About/About";
import Contact from "./Contact/Contact";

import Property from "./Properties/Property/Property";

import Login from "./Auth/Login/Login";

import "./assets/css/nav.scss";
import "./assets/css/plugins.css";
import "./assets/css/styles.css";
import "./assets/css/colors.css";
import Profile from "./Auth/Profile/Profile.js";
import { initialUserInfo } from "./redux/actions/authActions.js";

export const prod = process.env.NODE_ENV === "production" ? true : false;
export const link = prod ? "" : "http://localhost:3000";

function App() {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(initialUserInfo());
  }, [localStorage.getItem("userInfo")]);

  return (
    <Router history={history}>
      <div id="main-wrapper">
        <Navigation />
        <div className="clearfix"></div>
        <Switch>
          <Route exact path="/" component={Home} />
          <Route exact path="/properties" component={Properties} />
          <Route
            exact
            path="/property/:pId"
            render={(routeProps) => <Property {...routeProps} />}
          />
          <Route exact path="/about-us" component={About} />
          <Route exact path="/contact" component={Contact} />
          <Route exact path="/login" component={Login} />
          <PrivateRoute exact path="/add-new-property" component={Profile} />
          <PrivateRoute exact path="/profile" component={Profile} />
        </Switch>
        <div className="clearfix"></div>
        <Footer />
      </div>
    </Router>
  );
}

export default App;
