import { PROPERTY_PAGE_SUCCESS } from "../types";

import { FETCH_PROPERTY_PAGE } from "../links";

export const setPropertyPageSuccess = (data) => ({
  type: PROPERTY_PAGE_SUCCESS,
  payload: data,
});

export const fetchPropertyPage = (pId) => {
  return (dispatch) => {
    fetch(FETCH_PROPERTY_PAGE(pId))
      .then((res) => res.json())
      .then((json) => {
        if (json.status === "success") {
          dispatch(setPropertyPageSuccess(json.data.data));
        }
      })
      .catch((err) => console.error(err));
  };
};
