import { SIDEBAR_PROPERTIES_SUCCESS } from "../types.js";

const initialState = {
  properties: [],
};

const sidebarReducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case SIDEBAR_PROPERTIES_SUCCESS:
      return {
        ...state,
        properties: payload,
      };
    default:
      return state;
  }
};

export default sidebarReducer;
