import { SET_USER_PROPERTIES } from "../types";
import { FETCH_USER_PROPERTIES_NEW } from "../links";

export const setUserPropertiesAction = (data) => ({
  type: SET_USER_PROPERTIES,
  payload: data,
});

export const fetchUserProperties = (email) => {
  return (dispatch) => {
    fetch(FETCH_USER_PROPERTIES_NEW + email)
      .then((res) => res.json())
      .then((json) => dispatch(setUserPropertiesAction(json.data)))
      .catch((err) => console.error("User Properties Error: ", err));
  };
};
