import {
  SET_USER_INFO_SUCCESS,
  SET_USER_INFO_ERROR,
  INITIAL_USER_INFO,
  LOGOUT_ACTION
} from "../types";

const initialState = {
  user: {},
  jwt: "",
  error: ""
};

const authReducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case SET_USER_INFO_SUCCESS:
      return {
        ...state,
        user: payload.user,
        jwt: payload.jwt,
        error: ""
      };
    case INITIAL_USER_INFO:
      return {
        ...state,
        user: payload.user,
        jwt: payload.jwt
      };
    case SET_USER_INFO_ERROR:
      return {
        ...state,
        error: payload
      };
    case LOGOUT_ACTION:
      return {
        ...state,
        user: {},
        jwt: "",
        error: ""
      };
    default:
      return state;
  }
};

export default authReducer;
