import { SIDEBAR_PROPERTIES_SUCCESS } from "../types";

import { FETCH_SIDEBAR } from "../links";

export const setSidebarSuccess = (data) => ({
  type: SIDEBAR_PROPERTIES_SUCCESS,
  payload: data,
});

export const fetchSidebar = () => {
  return (dispatch) => {
    fetch(FETCH_SIDEBAR)
      .then((res) => res.json())
      .then((json) => {
        if (json.status === "success") {
          dispatch(setSidebarSuccess(json.data.data));
        }
      })
      .catch((err) => console.error(err));
  };
};
