import React from "react";

import logo from "../../assets/images/logo.png";
import { Link, useLocation } from "react-router-dom";
import UserMenu from "./components/UserMenu";

const Navigation = () => {
  const { pathname } = useLocation();
  const [toggleMobile, setToggleMobile] = React.useState(false);

  return (
    <div className="header header-light">
      <nav
        className={
          toggleMobile
            ? "headnavbar core-nav open-responsive open-dropdown"
            : "headnavbar core-nav"
        }
      >
        <div className="nav-container">
          <div className="nav-header right">
            <Link to="/" className="brand">
              <img src={logo} alt="" />
            </Link>
            <button
              className="toggle-bar core-nav-toggle"
              onClick={() => setToggleMobile(!toggleMobile)}
            >
              <span className="ti-align-justify"></span>
            </button>
          </div>
          <div className="wrap-core-nav-list right">
            <UserMenu />
            <ul className="attributes">
              <li className="login-attri theme-log">
                <Link to="/contact">Contact</Link>
              </li>
            </ul>
            <ul className="menu core-nav-list">
              <li className="dropdown">
                <Link to="/" className={pathname === "/" ? "active" : ""}>
                  Home
                </Link>
              </li>

              <li className="dropdown">
                <Link
                  to="/properties"
                  className={pathname === "/properties" ? "active" : ""}
                >
                  Listings
                </Link>
              </li>

              <li className="dropdown">
                <Link
                  to="/about-us"
                  className={pathname === "/about-us" ? "active" : ""}
                >
                  About Us
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </div>
  );
};

export default Navigation;
