import React, { useState, useEffect } from "react";
import { useDispatch, shallowEqual, useSelector } from "react-redux";

import Slider from "react-slick";
import { Link } from "react-router-dom";
import { link } from "../../../../../App";
import { fetchSidebar } from "../../../../../redux/actions/sidebarActions";

const Sidebar = ({ agent }) => {
  const dispatch = useDispatch();

  const properties = useSelector(
    (state) => state.sidebarReducer.properties,
    shallowEqual
  );

  useEffect(() => {
    if (properties.length < 1) {
      dispatch(fetchSidebar());
    }
  }, []);

  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };

  return (
    <div className="col-lg-4 col-md-12 col-sm-12">
      <div className="page-sidebar">
        <div className="agent-widget">
          <div className="agent-title">
            <div className="agent-photo">
              <img
                src={
                  agent
                    ? `${link}/img/users/${agent.photo}`
                    : `${link}/img/users/default.jpg`
                }
                alt=""
              />
            </div>
            <div className="agent-details">
              <h4>
                <a href="#">{agent ? agent.name : "Unknown"}</a>
              </h4>
              <span>
                <i className="lni-phone-handset"></i>
                {agent ? agent.telephone : "Unknown"}{" "}
              </span>
            </div>
            <div className="clearfix"></div>
          </div>

          <div className="form-group">
            <input
              type="text"
              className="form-control"
              placeholder="Your Email"
            />
          </div>
          <div className="form-group">
            <input
              type="text"
              className="form-control"
              placeholder="Your Phone"
            />
          </div>
          <div className="form-group">
            <textarea className="form-control">
              I'm interested in this property.
            </textarea>
          </div>
          <button className="btn btn-theme full-width">Send Message</button>
        </div>

        <div className="sidebar-widgets">
          <h4>Featured Property</h4>

          <div className="sidebar-property-slide">
            <Slider {...settings}>
              {properties.map((property) => {
                return (
                  <div className="single-items">
                    <div className="property-listing property-1">
                      <div className="listing-img-wrapper">
                        <Link to={`/property/${property.url}`}>
                          <img
                            src={`${link}/img/properties/${property.mainImage}`}
                            className="img-fluid mx-auto"
                            alt=""
                          />
                        </Link>
                        <div className="listing-like-top">
                          <i className="ti-heart"></i>
                        </div>
                        <div className="listing-rating">
                          <i className="ti-star filled"></i>
                          <i className="ti-star filled"></i>
                          <i className="ti-star filled"></i>
                          <i className="ti-star filled"></i>
                          <i className="ti-star"></i>
                        </div>
                        <span className="property-type">
                          For {property.basic.status}
                        </span>
                      </div>

                      <div className="listing-content">
                        <div className="listing-detail-wrapper">
                          <div className="listing-short-detail">
                            <h4 className="listing-name">
                              <Link to={`/property/${property.url}`}>
                                {property.title}
                              </Link>
                            </h4>
                            <span className="listing-location">
                              <i className="ti-location-pin"></i>
                              {property.location.address}
                            </span>
                          </div>
                          <div className="list-author">
                            <Link to={`/property/${property.url}`}>
                              <img
                                src={
                                  property.agent
                                    ? `${link}/img/users/${property.agent.photo}`
                                    : `${link}/img/users/default.jpg`
                                }
                                className="img-fluid img-circle avater-30"
                                alt=""
                              />
                            </Link>
                          </div>
                        </div>

                        <div className="listing-features-info">
                          <ul>
                            <li>
                              <strong>Bed:</strong>
                              {property.basic.bedrooms}
                            </li>
                            <li>
                              <strong>Bath:</strong>
                              {property.basic.bathrooms}
                            </li>
                            <li>
                              <strong>Sqft:</strong>
                              {property.basic.sqft}
                            </li>
                          </ul>
                        </div>

                        <div className="listing-footer-wrapper">
                          <div className="listing-price">
                            <h4 className="list-pr">${property.basic.price}</h4>
                          </div>
                          <div className="listing-detail-btn">
                            <Link
                              to={`/property/${property.url}`}
                              className="more-btn"
                            >
                              More Info
                            </Link>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                );
              })}
            </Slider>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Sidebar;
