import {
  PROPERTY_LIST_SUCCESS,
  NUMBER_OF_PROPERTIES_SUCCESS,
  CHANGE_PAGE_NUMBER,
} from "../types";

import { FETCH_PROPERTY_LIST, FETCH_NUMBER_OF_PROPERTIES } from "../links";

export const setPropertyList = (pageNumber, data) => ({
  type: PROPERTY_LIST_SUCCESS,
  payload: { pageNumber, data },
});

export const setNumberOfProperties = (number) => ({
  type: NUMBER_OF_PROPERTIES_SUCCESS,
  payload: number,
});

export const changePageNumber = (number) => ({
  type: CHANGE_PAGE_NUMBER,
  payload: number,
});

export const fetchPropertyList = (
  pageNumber,
  propertyPerPage,
  count = true
) => {
  return (dispatch) => {
    fetch(FETCH_PROPERTY_LIST(pageNumber, propertyPerPage))
      .then((res) => res.json())
      .then((json) => {
        if (json.status === "success") {
          dispatch(setPropertyList(pageNumber, json.data.data));
        }
      });
    if (count) {
      fetch(FETCH_NUMBER_OF_PROPERTIES)
        .then((res) => res.json())
        .then((json) => {
          if (json.status === "success") {
            dispatch(setNumberOfProperties(json.count));
          }
        });
    }
  };
};
