import React, { useState, useEffect } from "react";
import Slider from "react-slick";
import "./styles/ImageSlider.scss";
import SliderInfo from "./SliderInfo";

import GalleryImages from "../dummyGallery/gallery";
import { link } from "../../../../../App";

const ImageSlider = (props) => {
  let mainImageStateInitial = `${link}/img/properties/${props.mainImage}`;

  const [mainImageState, setMainImageState] = useState(mainImageStateInitial);

  const handleGalleryChange = (url) => {
    setMainImageState(url);
  };

  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 6,
    slidesToScroll: 1,
  };

  return (
    <>
      <div className="col-lg-7 col-md-7 col-sm-12">
        <div className="item-slick">
          <img style={{ width: "100%" }} src={mainImageState} alt="Alt" />
        </div>
      </div>
      <SliderInfo {...props} />
      <div className="col-lg-12 col-md-12 col-sm-12">
        <div className="slick-for">
          <Slider {...settings}>
            <div
              className="item-slick"
              onClick={() => handleGalleryChange(mainImageStateInitial)}
            >
              <img src={mainImageStateInitial} alt="Alt" />
            </div>
            {props.gallery.map((image, index) => {
              return (
                <div
                  key={index}
                  className="item-slick"
                  onClick={() =>
                    handleGalleryChange(`${link}/img/properties/${image}`)
                  }
                >
                  <img
                    src={`${link}/img/properties/${image}`}
                    alt={index + "-image"}
                  />
                </div>
              );
            })}
            {props.gallery.length < 6
              ? GalleryImages.map((image, index) => {
                  return (
                    <div
                      key={index}
                      className="item-slick"
                      onClick={() => handleGalleryChange(image)}
                    >
                      <img src={image} alt={index + "-image"} />
                    </div>
                  );
                })
              : null}
          </Slider>
        </div>
      </div>
    </>
  );
};

export default ImageSlider;
