import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";

import { useSelector, shallowEqual, useDispatch } from "react-redux";
import { logout } from "../../../redux/actions/authActions";
import { Link } from "react-router-dom";

const Sidebar = () => {
  const { pathname } = useLocation();
  const dispatch = useDispatch();

  const [agentImageState, setAgentImageState] = useState("");

  const userInfo = useSelector(state => state.authReducer.user, shallowEqual);

  const images = require.context("../../../assets/demo-imgs", true);

  useEffect(() => {
    if (userInfo.photo) {
      setAgentImageState(images("./" + userInfo.photo));
    }
  }, [userInfo]);

  return (
    <div className="col-lg-4 col-md-12">
      <div className="dashboard-navbar">
        <div className="d-user-avater">
          <img
            src={
              agentImageState === ""
                ? "https://via.placeholder.com/400x400"
                : agentImageState
            }
            className="img-fluid avater"
            alt=""
          />
          <h4>{userInfo.name}</h4>
          <span>{userInfo.email}</span>
        </div>

        <div className="d-navigation">
          <ul>
            <li className={pathname === "/profile" ? "active" : ""}>
              <Link to="/profile">
                <i className="ti-layers"></i>My Properties
              </Link>
            </li>
            <li className={pathname === "/add-new-property" ? "active" : ""}>
              <Link to="/add-new-property">
                <i className="ti-pencil-alt"></i>Submit New Property
              </Link>
            </li>

            <li>
              <a href="#" onClick={() => dispatch(logout())}>
                <i className="ti-power-off"></i>Log Out
              </a>
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default Sidebar;
