import { SET_USER_PROPERTIES } from "../types.js";

const initialState = {
  properties: [],
  loading: true
};

const mypropertiesReducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case SET_USER_PROPERTIES:
      return {
        ...state,
        properties: payload.data,
        loading: false
      };

    default:
      return state;
  }
};

export default mypropertiesReducer;
