import { HOME_FEATURED_SUCCESS } from "../types.js";

const initialState = {
  properties: [],
  propertiesNumber: 0,
};

const homeReducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case HOME_FEATURED_SUCCESS:
      return {
        ...state,
        properties: payload.data,
        propertiesNumber: payload.results,
      };

    default:
      return state;
  }
};

export default homeReducer;
