import React from "react";
import Sidebar from "./components/Sidebar";
import MortgageCalculator from "./components/MortgageCalculator";

const Details = ({ basic, detailedInfo, location, agent }) => {
  return (
    <section className="gray">
      <div className="container">
        <div className="row">
          <div className="col-lg-8 col-md-12 col-sm-12">
            <div className="block-wrap">
              <div className="block-header">
                <h4 className="block-title">Property Info</h4>
              </div>

              <div className="block-body">
                <ul className="dw-proprty-info">
                  <li>
                    <strong>Bedrooms</strong>
                    {basic.bedrooms}
                  </li>
                  <li>
                    <strong>Bathrooms</strong>
                    {basic.bathrooms}
                  </li>
                  <li>
                    <strong>Garage</strong>
                    {detailedInfo.garage}
                  </li>
                  <li>
                    <strong>Area</strong>
                    {basic.sqft} sq ft
                  </li>
                  <li>
                    <strong>Type</strong>
                    {basic.propertyType}
                  </li>
                  <li>
                    <strong>Price</strong>${basic.price}
                  </li>
                  <li>
                    <strong>City</strong>
                    {location.city}
                  </li>
                  <li>
                    <strong>Build On</strong>
                    {2020 - detailedInfo.buildingAge}
                  </li>
                </ul>
              </div>
            </div>

            <div className="block-wrap">
              <div className="block-header">
                <h4 className="block-title">Description</h4>
              </div>

              <div className="block-body">
                <p>{detailedInfo.description}</p>
              </div>
            </div>

            <div className="block-wrap">
              <div className="block-header">
                <h4 className="block-title">Ameneties</h4>
              </div>

              <div className="block-body">
                <ul className="avl-features third">
                  {Object.keys(detailedInfo.otherFeatures).map(feature => {
                    return (
                      <li
                        className={
                          detailedInfo.otherFeatures[feature] ? "check" : ""
                        }
                      >
                        {feature}
                      </li>
                    );
                  })}
                </ul>
              </div>
            </div>
            {basic.status === "sale" ? <MortgageCalculator /> : null}
          </div>
          <Sidebar agent={agent} />
        </div>
      </div>
    </section>
  );
};

export default Details;
