import React, { useState, useEffect } from "react";
import { useDispatch, shallowEqual, useSelector } from "react-redux";
import SingleProperty from "./components/SingleProperty";
import { link } from "../../../App";
import { fetchHomeFeatured } from "../../../redux/actions/homeActions";

const FeaturedProperties = () => {
  const dispatch = useDispatch();

  const properties = useSelector(
    (state) => state.homeReducer.properties,
    shallowEqual
  );

  useEffect(() => {
    if (properties.length === 0) {
      dispatch(fetchHomeFeatured());
    }
  }, []);

  return (
    <section className="gray">
      <div className="container">
        <div className="row">
          <div className="col-lg-12 col-md-12">
            <div className="sec-heading center">
              <h2>Browse All Property</h2>
              <p>Popular properties by our expert agents</p>
            </div>
          </div>
        </div>

        <div className="row">
          {properties.map((featured, index) => {
            return <SingleProperty key={index} {...featured} />;
          })}
        </div>
      </div>
    </section>
  );
};

export default FeaturedProperties;
