import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { useDispatch, shallowEqual, useSelector } from "react-redux";

import Slider from "./components/Slider/Slider";
import SeperaterInfo from "./components/SeperaterInfo";
import Details from "./components/Details/Details";

import { fetchPropertyPage } from "../../redux/actions/propertyActions";

const Property = () => {
  let { pId } = useParams();
  const dispatch = useDispatch();

  const [propertyInfo, setPropertyInfo] = useState({});

  const properties = useSelector(
    (state) => state.propertyReducer.properties,
    shallowEqual
  );

  useEffect(() => {
    if (Object.keys(properties).includes(pId)) {
      setPropertyInfo(properties[pId]);
    } else {
      dispatch(fetchPropertyPage(pId));
    }
  }, [pId, properties]);

  window.scrollTo(0, 0);

  useEffect(() => {
    console.error("INFO", propertyInfo);
  }, [propertyInfo]);

  if (Object.keys(propertyInfo).length > 0) {
    return (
      <>
        <Slider {...propertyInfo} />
        <SeperaterInfo {...propertyInfo} />
        <Details {...propertyInfo} />
      </>
    );
  } else {
    return <h1>Loading...</h1>;
  }
};

export default Property;
