import React from "react";
import { useSelector, shallowEqual } from "react-redux";

import PageTitle from "../../shared/Headers/PageTitle";
import Sidebar from "./components/Sidebar";
import MyProperties from "./components/MyProperties/MyProperties";
import history from "../../history";
import NewProperty from "./components/NewProperty";

const Profile = () => {
  const userInfo = useSelector(state => state.authReducer.user, shallowEqual);

  return (
    <>
      <PageTitle title={"Profile Page"} desc={`Welcome ${userInfo.name}`} />
      <section>
        <div class="container">
          <div class="row">
            <Sidebar />
            {history.location.pathname.replace("/", "") === "profile" ? (
              <MyProperties />
            ) : null}
            {history.location.pathname.replace("/", "") ===
            "add-new-property" ? (
              <NewProperty />
            ) : null}
          </div>
        </div>
      </section>
    </>
  );
};

export default Profile;
