import {
  SET_USER_INFO_SUCCESS,
  SET_USER_INFO_ERROR,
  INITIAL_USER_INFO,
  LOGOUT_ACTION,
} from "../types";
import { FETCH_USER_INFO_NEW } from "../links";

import request from "../../Auth/utils/request";
import auth from "../../Auth/utils/auth";

export const setUserInfoSuccess = (user, jwt) => ({
  type: SET_USER_INFO_SUCCESS,
  payload: { user, jwt },
});

export const setUserInfoError = (err) => ({
  type: SET_USER_INFO_ERROR,
  payload: err,
});

export const initialUserInfoAction = (user, jwt) => ({
  type: INITIAL_USER_INFO,
  payload: { user, jwt },
});

export const logoutAction = () => ({
  type: LOGOUT_ACTION,
});

export const fetchUserInfo = (data) => {
  return (dispatch) => {
    request(FETCH_USER_INFO_NEW, { method: "POST", body: data })
      .then((response) => {
        if (response.status === "success") {
          auth.setToken(response.token, true);
          auth.setUserInfo(response.data.user, true);
          dispatch(setUserInfoSuccess(response.data.user, response.token));
        } else {
          throw new Error("problem on response-", response);
        }
      })
      .catch((err) => {
        const errors = [
          { name: "login", errors: [err.response.payload.message] },
        ];
        dispatch(setUserInfoError(errors));
      });
  };
};

export const initialUserInfo = () => {
  return (dispatch) => {
    if (auth.getToken() !== null) {
      dispatch(initialUserInfoAction(auth.getUserInfo(), auth.getToken()));
    }
  };
};

export const logout = () => {
  return (dispatch) => {
    auth.clearAppStorage();
    dispatch(logoutAction());
  };
};
