import {} from "../types.js";

const initialState = {
  featuredCid: ""
};

const mainReducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case "TEST":
      return {
        ...state,
        featuredCid: "TEST IS WORKING"
      };

    default:
      return state;
  }
};

export default mainReducer;
