import React, { useState, useEffect } from "react";
import { useSelector, shallowEqual, useDispatch } from "react-redux";
import history from "../../history";
import { fetchUserInfo } from "../../redux/actions/authActions";

const Login = () => {
  const dispatch = useDispatch();
  const [authState, setAuthState] = useState({ email: "", password: "" });

  const handleChange = e => {
    const { name, value } = e.target;
    setAuthState({
      ...authState,
      [name]: value
    });
  };

  const jwtState = useSelector(state => state.authReducer.jwt, shallowEqual);

  const handleSubmit = e => {
    e.preventDefault();
    dispatch(fetchUserInfo(authState));
  };

  useEffect(() => {
    if (jwtState !== "") {
      history.push("/profile");
    }
  }, [jwtState]);

  return (
    <div
      className="modal-dialog modal-dialog-centered login-pop-form"
      role="document"
    >
      <div className="modal-content" id="registermodal">
        <div className="modal-body">
          <h4 className="modal-header-title">Log In</h4>
          <div className="login-form">
            <form onSubmit={handleSubmit}>
              <div className="form-group">
                <label>User Name</label>
                <div className="input-with-icon">
                  <input
                    type="text"
                    name="email"
                    className="form-control"
                    placeholder="Username"
                    value={authState.email}
                    onChange={handleChange}
                  />
                  <i className="ti-user"></i>
                </div>
              </div>

              <div className="form-group">
                <label>Password</label>
                <div className="input-with-icon">
                  <input
                    type="password"
                    name="password"
                    className="form-control"
                    placeholder="*******"
                    value={authState.password}
                    onChange={handleChange}
                  />
                  <i className="ti-unlock"></i>
                </div>
              </div>

              <div className="form-group">
                <button
                  type="submit"
                  className="btn btn-md full-width pop-login"
                >
                  Login
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Login;
