import React, { useEffect } from "react";
import { useSelector, shallowEqual, useDispatch } from "react-redux";

import MyPropertyItem from "./components/MyPropertyItem";
import { fetchUserProperties } from "../../../../redux/actions/mypropertiesActions";

const MyProperties = () => {
  const dispatch = useDispatch();

  const userInfo = useSelector(state => state.authReducer.user, shallowEqual);

  const userProperties = useSelector(
    state => state.mypropertiesReducer.properties,
    shallowEqual
  );

  useEffect(() => {
    if (userInfo._id) {
      dispatch(fetchUserProperties(userInfo._id));
    }
  }, [userInfo]);

  return (
    <div className="col-lg-8 col-md-12">
      <div className="dashboard-wraper">
        <div className="form-submit">
          <h4>My Property</h4>
        </div>

        <table className="property-table-wrap responsive-table">
          <tbody>
            <tr>
              <th>
                <i className="fa fa-file-text"></i> Property
              </th>
              <th className="expire-date">
                <i className="fa fa-calendar"></i> Expiration Date
              </th>
              <th></th>
            </tr>
            {userProperties.map(property => {
              return <MyPropertyItem key={property.id} {...property} />;
            })}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default MyProperties;
