import { HOME_FEATURED_SUCCESS } from "../types";

import { FETCH_HOME_FEATURED_PROPERTIES } from "../links";

export const setHomeFeaturedSuccess = ({ data, results }) => ({
  type: HOME_FEATURED_SUCCESS,
  payload: { data: data.data, results },
});

export const fetchHomeFeatured = () => {
  return (dispatch) => {
    fetch(FETCH_HOME_FEATURED_PROPERTIES)
      .then((res) => res.json())
      .then((json) => {
        if (json.status === "success") {
          dispatch(setHomeFeaturedSuccess(json));
        }
      })
      .catch((err) => console.error(err));
  };
};
