import React, { useState, useEffect } from "react";

const Pagination = ({ count, perPage, handlePagination, pageNumber }) => {
  const [arrayPerPage, setArrayPerPage] = useState([]);
  useEffect(() => {
    let tempArray = [];
    for (let index = 1; index <= count / perPage; index++) {
      tempArray.push(index);
    }
    if (count > perPage * tempArray[tempArray.length - 1]) {
      tempArray.push(tempArray[tempArray.length - 1] + 1);
    }
    setArrayPerPage([...tempArray]);
  }, []);

  return (
    <div className="row">
      <div className="col-lg-12 col-md-12 col-sm-12">
        <ul className="pagination p-center">
          {pageNumber !== 1 && (
            <li className="page-item">
              <span
                className="page-link"
                style={{ cursor: "pointer" }}
                aria-label="Previous"
                onClick={() => handlePagination("previous")}
              >
                <span className="ti-arrow-left"></span>
                <span className="sr-only">Previous</span>
              </span>
            </li>
          )}
          {arrayPerPage.map((array, index) => {
            return (
              <li
                key={index}
                className={
                  index + 1 === pageNumber ? "page-item active" : "page-item"
                }
              >
                <span
                  onClick={() => handlePagination(index + 1)}
                  className="page-link"
                  style={{ cursor: "pointer" }}
                >
                  {index + 1}
                </span>
              </li>
            );
          })}

          {arrayPerPage.length > pageNumber && (
            <li className="page-item">
              <span
                onClick={() => handlePagination("next")}
                className="page-link"
                style={{ cursor: "pointer" }}
                aria-label="Next"
              >
                <span className="ti-arrow-right"></span>
                <span className="sr-only">Next</span>
              </span>
            </li>
          )}
        </ul>
      </div>
    </div>
  );
};

export default Pagination;
