import React from "react";
import { Link } from "react-router-dom";
import { FormattedNumber } from "react-intl";

const MyPropertyItem = props => {
  const images = require.context("../../../../../assets/demo-imgs", true);
  let mainImageState = images("./" + props.mainImage);

  return (
    <tr>
      <td className="property-container">
        <img
          src={
            !props.mainImage
              ? "https://via.placeholder.com/1280x880"
              : mainImageState
          }
          alt=""
        />
        <div className="title">
          <h4>
            <Link to={`/property/${props.url}`}>{props.title}</Link>
          </h4>
          <span>{props.location.address}</span>
          <span className="table-property-price">
            <FormattedNumber
              value={props.basic.price}
              style="currency"
              currency="CAD"
            />
            {props.basic.status === "rent" ? " / monthly" : ""}
          </span>
          <span className="table-property-price">{props.basic.status}</span>
        </div>
      </td>
      <td className="expire-date">{props.expirationDay.substring(0, 10)}</td>
      <td className="action">
        <a href="#">
          <i className="ti-pencil"></i> Edit
        </a>
        <a href="#">
          <i className="ti-eye"></i> Hide
        </a>
        <a href="#" className="delete">
          <i className="ti-close"></i> Delete
        </a>
      </td>
    </tr>
  );
};

export default MyPropertyItem;
