import {
  PROPERTY_LIST_SUCCESS,
  NUMBER_OF_PROPERTIES_SUCCESS,
  CHANGE_PAGE_NUMBER,
} from "../types.js";

const initialState = {
  pageNumber: 1,
  properties: { 1: [] },
  numberOfProperties: 0,
};

const propertyListReducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case PROPERTY_LIST_SUCCESS:
      return {
        ...state,
        pageNumber: payload.pageNumber,
        properties: {
          ...state.properties,
          [payload.pageNumber]: payload.data,
        },
      };
    case NUMBER_OF_PROPERTIES_SUCCESS:
      return {
        ...state,
        numberOfProperties: payload,
      };
    case CHANGE_PAGE_NUMBER:
      return {
        ...state,
        pageNumber: payload,
      };
    default:
      return state;
  }
};

export default propertyListReducer;
