import { PROPERTY_PAGE_SUCCESS } from "../types.js";

const initialState = {
  properties: {},
};

const propertyReducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case PROPERTY_PAGE_SUCCESS:
      return {
        ...state,
        properties: {
          ...state.properties,
          [payload.url]: payload,
        },
      };

    default:
      return state;
  }
};

export default propertyReducer;
