import React from "react";
import { FormattedNumber } from "react-intl";

const SliderInfo = ({ title, basic, detailedInfo }) => {
  return (
    <div className="col-lg-5 col-md-5 col-sm-12">
      <div className="single-advance-caption">
        <div className="property-name-info">
          <h4 className="property-name">{title}</h4>
        </div>

        <div className="property-price-info">
          <h4 className="property-price">
            <FormattedNumber
              value={basic.price}
              style="currency"
              currency="CAD"
            />
          </h4>
          {basic.status === "sale" ? (
            <p className="property-sqa">
              {basic.sqft}
              <sub>m2</sub> (
              <FormattedNumber
                value={basic.price / basic.sqft}
                style="currency"
                currency="CAD"
              />
              $/m2)
            </p>
          ) : null}
        </div>

        <div className="property-statement">
          <ul>
            <li>
              <i className="lni-apartment"></i>
              <div className="ps-trep">
                <span>Type</span>
                <h5 className="ps-type">{basic.propertyType}</h5>
              </div>
            </li>
            <li>
              <i className="lni-restaurant"></i>
              <div className="ps-trep">
                <span>Build On</span>
                <h5 className="ps-type">{2020 - detailedInfo.buildingAge}</h5>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default SliderInfo;
